.profile {
	display: flex;
	flex-direction: column;
	align-items: center;
	min-height: 100vh;
	background-color: var(--paletteColor3);
	padding-top: 50px;

	& h1 {
		color: var(--paletteColor8);
		font-size: 28px;
		margin-bottom: 50px;
	}

	& img {
		border-radius: 50%;
		border: 1px solid var(--paletteColor9);
	}
}